
.accent-check-label {
    display: grid;
    grid-template-columns: 1em auto;
    gap: 0.5em;
  }
  
  .accent-check-label input[type="checkbox"] {
    -webkit-appearance: none;
    appearance: none;
    background-color: #fff;
    margin: 0;
    font: inherit;
    color: var(--accent);
    width: 1.15em;
    height: 1.15em;
    border: 0.15em solid var(--accent);
    border-radius: 0.15em;
    transform: translateY(-0.075em);
    display: grid;
    place-content: center;
  }
  
  .accent-check-label input[type="checkbox"]::before {
    content: "";
    width: 0.65em;
    height: 0.65em;
    transform: scale(0);
    transition: 120ms transform ease-in-out;
    box-shadow: inset 1em 1em var(--accent);
  }
  
  .accent-check-label input[type="checkbox"]:checked::before {
    transform: scale(1);
  }
  